import { Component, Input, ChangeDetectorRef, Output, EventEmitter } from '@angular/core';
import { FormArray, FormGroup } from '@angular/forms';

import { TravellerType } from '@core/constants/traveller-type';
import { CheckoutTraveller } from '@schemas/modules/checkout/cart-traveller';
import { UserService } from '@services/modules/user/services/user.service';
import { ServiceId } from '@core/constants/service-id.const';
import { CartTravellerInfo } from '@schemas/modules/checkout/cart-traveller';
import { Passenger } from '@shared/schemas/tz-customer-info';
import { CheckoutCartServiceSummary } from '@schemas/modules/checkout/cart-summary';
import { DateRangePipe } from '@shared/pipes/date-range.pipe';
import { DatePipe } from '@angular/common';
import { Trip } from '@core/constants/trip';
import { Phone } from '@shared/schemas/tz-customer-info';
import { PlatformService } from '@services/platform.service';

@Component({
    selector: 'tz-checkout-travellers',
    templateUrl: './travellers.component.html',
    styleUrls: ['./travellers.component.scss'],
})
export class TravellersComponent {
    @Input() checkout: any;
    @Input() travellersForm = new FormGroup({});
    @Input() cartInfo!: CartTravellerInfo;
    @Input() cartSummary: CheckoutCartServiceSummary[] = [];

    @Output() markFormAsVisited = new EventEmitter<string>();

    public serviceIds = ServiceId;

    constructor(
        private readonly changeDetectorRef: ChangeDetectorRef,
        private userService: UserService,
        private dateRange: DateRangePipe,
        private datePipe: DatePipe,
        public platformService: PlatformService
    ) {}

    get transportFormArray(): FormGroup[] {
        const formArray = this.travellersForm?.get('transport') as FormArray;
        return formArray.controls as FormGroup[];
    }

    get hotelFormArray(): FormGroup[] {
        const formArray = this.travellersForm?.get('hotel') as FormArray;
        return formArray.controls as FormGroup[];
    }

    get transferFormGroup(): FormGroup {
        return this.travellersForm?.get('transfer') as FormGroup;
    }

    get rentalFormGroup(): FormGroup {
        return this.travellersForm?.get('rental') as FormGroup;
    }

    get serviceTitle(): { [key: string]: string } {
        const serviceTitleMap: { [key: string]: string } = {};
        const transferDates: string[] = [];
        const trainDates: string[] = [];
        const isMobile = this.platformService.isMobileDevice;
        const maxAddrLength = isMobile ? 45 : 60;

        for (const summary of this.cartSummary) {
            switch (summary.serviceId) {
                case ServiceId.FLIGHT: {
                    const travelDate =
                        summary.flyType === Trip.ROUND_TRIP
                            ? this.dateRange.transform(summary.departureTzFormat, summary.arrivalTzFormat)
                            : this.datePipe.transform(summary.departureTzFormat, 'mediumDate');
                    const addr = `${summary.originCity} (${summary.originCode}) 
                    ${summary.destinationCity} (${summary.destinationCode})`;

                    serviceTitleMap[ServiceId.FLIGHT_TRAIN] = `${
                        addr.length > maxAddrLength ? addr.substring(0, maxAddrLength) + '...' : addr
                    } | ${travelDate}`;
                    break;
                }
                case ServiceId.TRAIN: {
                    const addr = `${summary.originCity}  ${summary.destinationCity}`;
                    if (!serviceTitleMap[ServiceId.FLIGHT_TRAIN] || serviceTitleMap[ServiceId.FLIGHT_TRAIN] === '') {
                        serviceTitleMap[ServiceId.FLIGHT_TRAIN] = `${
                            addr.length > maxAddrLength ? addr.substring(0, maxAddrLength) + '...' : addr
                        }`;
                    }

                    trainDates.push(summary.departureTzFormat);
                    break;
                }
                case ServiceId.ACC: {
                    const date = this.dateRange.transform(summary.checkIn, summary.checkOut);
                    const addr =
                        summary.hotelName.length > maxAddrLength - 20
                            ? summary.hotelName.substring(0, maxAddrLength - 20) + '...'
                            : summary.hotelName;
                    serviceTitleMap[ServiceId.ACC] = `${addr} | ${date}`;
                    break;
                }
                case ServiceId.TRANSFER: {
                    transferDates.push(summary.pickupTzDate);
                    break;
                }
                case ServiceId.CAR_RENTAL: {
                    const date = this.dateRange.transform(summary.pickupTzDate, summary.dropOffTzDate);
                    serviceTitleMap[ServiceId.CAR_RENTAL] = `${summary.class} | ${date}`;
                    break;
                }
            }
        }

        if (trainDates.length > 0) {
            serviceTitleMap[ServiceId.FLIGHT_TRAIN] += ` | ${
                trainDates.length === 2
                    ? this.dateRange.transform(trainDates[0], trainDates[1])
                    : this.datePipe.transform(trainDates[0], 'mediumDate')
            }`;
        }

        if (transferDates.length > 0) {
            serviceTitleMap[ServiceId.TRANSFER] =
                transferDates.length === 2
                    ? this.dateRange.transform(transferDates[0], transferDates[1])
                    : this.datePipe.transform(transferDates[0], 'mediumDate') + '';
        }

        return serviceTitleMap;
    }

    public setTraveller(serviceId: ServiceId, traveller: Passenger, idx: number, fromSavedTravellers = false): void {
        traveller.middleName = traveller.middleName ? traveller.middleName : '';
        switch (serviceId) {
            case ServiceId.FLIGHT_TRAIN: {
                const t = this.checkout.passengerDetails[idx];
                this.checkout.passengerDetails[idx] = {
                    ...traveller,
                    type: t.type,
                    selectedAge: t.selectedAge,
                    docNotRequired: t.docNotRequired,
                    additionalLuggage: t.additionalLuggage,
                    saveTraveller: t.saveTraveller,
                    updateTraveller: t.updateTraveller,
                    updateTravellerForm: fromSavedTravellers,
                };
                break;
            }
            case ServiceId.ACC: {
                const t = this.checkout.hotelPassengers[idx];
                this.checkout.hotelPassengers[idx] = {
                    ...traveller,
                    type: TravellerType.ADULT,
                    roomType: t.roomType,
                    roomOfferId: t.roomOfferId,
                    optionId: t.optionId,
                    updateTravellerForm: fromSavedTravellers,
                };
                break;
            }
            case ServiceId.TRANSFER: {
                if (this.userService.IsLogged() && fromSavedTravellers) {
                    const customer = this.userService.getCustomerInformation();
                    traveller.phone = {} as Phone;

                    if (
                        customer.phone &&
                        customer.phone.number !== '' &&
                        (customer.firstName.trim() + customer.lastName.trim()).replace(/ /g, '').toLowerCase() ===
                            (
                                traveller.firstName.trim() +
                                traveller.middleName.trim() +
                                traveller.lastName.trim()
                            ).toLowerCase()
                    ) {
                        traveller.phone = customer.phone;
                    }
                }

                this.checkout.transferPassenger = {
                    ...traveller,
                    type: TravellerType.ADULT,
                    updateTravellerForm: fromSavedTravellers,
                };
                break;
            }
            case ServiceId.CAR_RENTAL: {
                this.checkout.rentalPassenger = {
                    ...traveller,
                    type: TravellerType.ADULT,
                    selectedAge: this.checkout.rentalPassenger.selectedAge,
                    updateTravellerForm: fromSavedTravellers,
                };
                break;
            }
        }
        this.changeDetectorRef.detectChanges();
        console.log('Traveller updated: ', this.checkout);
    }

    public resetTraveller(serviceId: ServiceId, idx: number): void {
        switch (serviceId) {
            case ServiceId.FLIGHT_TRAIN: {
                const traveller = this.checkout.passengerDetails[idx];
                this.checkout.passengerDetails[idx] = {
                    type: traveller.type,
                    selectedAge: traveller.selectedAge,
                    docNotRequired: traveller.docNotRequired,
                    additionalLuggage: traveller.additionalLuggage,
                    saveTraveller: traveller.saveTraveller,
                    updateTraveller: traveller.updateTraveller,
                    updateTravellerForm: true,
                } as CheckoutTraveller;
                break;
            }
            case ServiceId.ACC: {
                const traveller = this.checkout.hotelPassengers[idx];
                this.checkout.hotelPassengers[idx] = {
                    type: TravellerType.ADULT,
                    roomType: traveller.roomType,
                    roomOfferId: traveller.roomOfferId,
                    optionId: traveller.optionId,
                    updateTravellerForm: true,
                } as CheckoutTraveller;
                break;
            }
            case ServiceId.TRANSFER: {
                this.checkout.transferPassenger = {
                    type: TravellerType.ADULT,
                    updateTravellerForm: true,
                } as CheckoutTraveller;
                break;
            }
            case ServiceId.CAR_RENTAL: {
                this.checkout.rentalPassenger = {
                    type: TravellerType.ADULT,
                    selectedAge: this.checkout.rentalPassenger.selectedAge,
                    updateTravellerForm: true,
                } as CheckoutTraveller;
                break;
            }
        }
        this.changeDetectorRef.detectChanges();
        console.log('Traveller reset: ', this.checkout);
    }
}
