import { Component, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { CheckoutTraveller } from '@schemas/modules/checkout/cart-traveller';
import { FlightBaggage } from '@schemas/modules/checkout/transport-luggage';
import { FlightLuggageOption, FlightLuggageOptionTypes } from '@schemas/modules/flights/flight-luggage.model';
import { ModalService } from '@services/modal.service';
import { TransportLuggagesUtil } from '@services/modules/checkout/utils/transport-luggages.utils';
import { TzCurrencyPipe } from '@shared/pipes/tz-currency.pipe';
import { MobPickerWheels } from '@schemas/modules/picker/mob-picker';
import { ToolsService } from '@core/utils/tools-service';

@Component({
    selector: 'tz-mob-additional-luggages',
    templateUrl: './mob-additional-luggages.component.html',
    styleUrls: ['./mob-additional-luggages.component.scss'],
})
export class MobAdditionalLuggagesComponent implements OnInit {
    public flightLuggage!: FlightBaggage;
    public passengerDetails!: CheckoutTraveller[];
    public transportLuggagesUtil = TransportLuggagesUtil;
    public bagTypes!: {
        txt: string;
        type: FlightLuggageOptionTypes.CABIN | FlightLuggageOptionTypes.CHECKIN;
    }[];
    public openedOptionsStringIdx = '';
    public selectedLuggageIndexesObj: { [key: number]: number } = {};

    private data!: {
        flightLuggages: FlightBaggage;
        checkout: any;
        passengerDetails: CheckoutTraveller[];
    };
    private listPriceCurrency!: string;
    private defaultPassengerDetails: {
        cabin: { bagsCount: number; listPrice: string };
        checkIn: { bagsCount: number; listPrice: string };
    }[] = [];

    constructor(
        public translateService: TranslateService,
        private tzCurrencyPipe: TzCurrencyPipe,
        private modalService: ModalService
    ) {}

    public get isPassengerDetailsChanged(): boolean {
        const newPassengerDetails = this.setDiffPassengerDetails();

        let isChanged = false;

        this.defaultPassengerDetails.map((item, index) => {
            const passengerDetailsItem = newPassengerDetails[index];

            const defaultCabin = passengerDetailsItem.cabin;
            const defaultCheckIn = passengerDetailsItem.checkIn;

            if (
                item.cabin.bagsCount !== defaultCabin.bagsCount ||
                item.cabin.listPrice !== defaultCabin.listPrice ||
                item.checkIn.bagsCount !== defaultCheckIn.bagsCount ||
                item.checkIn.listPrice !== defaultCheckIn.listPrice
            ) {
                isChanged = true;
            }
        });

        return isChanged;
    }

    public ngOnInit(): void {
        this.flightLuggage = this.data.flightLuggages;
        this.passengerDetails = this.data.passengerDetails;
        this.bagTypes = [
            {
                txt:
                    this.flightLuggage.checkIn && this.flightLuggage.checkIn.replaceIncluded
                        ? 'ADD_PASSENGER.Checked_bag_add'
                        : 'ADD_PASSENGER.Checked_bag',
                type: FlightLuggageOptionTypes.CHECKIN,
            },
        ];

        this.setDefaultPassengerDetails();
        this.setSelectedLuggageIndexes();
    }

    public getTravellerName(idx: number): string {
        return TransportLuggagesUtil.getTravellerName(this.data.passengerDetails[idx], idx, this.translateService);
    }

    public luggageCombinationAdd(idx: number, option: FlightLuggageOption, bagType: string): void {
        this.passengerDetails[idx].additionalLuggage = TransportLuggagesUtil.formLuggageCombination(
            this.passengerDetails[idx].additionalLuggage,
            option,
            bagType
        );

        this.listPriceCurrency = option.prices.listPriceCur;
    }

    public luggageCombinationRemove(idx: number, option: FlightLuggageOption, bagType: string): void {
        this.passengerDetails[idx].additionalLuggage = TransportLuggagesUtil.removeLuggageCombination(
            this.passengerDetails[idx].additionalLuggage,
            option,
            bagType
        );

        this.listPriceCurrency = option.prices.listPriceCur;
    }

    public confirmLuggageSelection(): void {
        this.modalService.dismiss({
            passengerDetails: this.passengerDetails,
            listPriceCurrency: this.listPriceCurrency,
        });
    }

    public openPicker(
        index: number,
        options: FlightLuggageOption[],
        bagType: string,
        passenger: CheckoutTraveller
    ): void {
        const wheels: MobPickerWheels[] = [
            { display: this.translateService.instant('FLIGHT.LUGGAGE.NO_BAG'), value: -1 },
        ];
        let selectedIndex: number | undefined;

        let i = 0;
        options.map((bagOption) => {
            if (this.transportLuggagesUtil.getOption(passenger.additionalLuggage, bagType, bagOption.index)?.count) {
                selectedIndex = i;
            }

            const price = this.tzCurrencyPipe.transform(bagOption.prices.listPrice, bagOption.prices.listPriceCur);
            wheels.push({
                display: bagOption.weightTexts.join(', ') + ' ' + price,
                value: i,
            });
            i++;
        });

        this.modalService
            .openPickerModal({ wheels, selected: [selectedIndex] }, { title: 'FLIGHT.LUGGAGE.SELECT' })
            .then((modal) => {
                if (modal.data?.selected) {
                    const modalSelectedItem = parseInt(modal.data?.selected[0], 10);
                    if (selectedIndex || selectedIndex === 0) {
                        this.luggageCombinationRemove(index, options[selectedIndex], bagType);
                    }

                    if (modalSelectedItem !== -1) {
                        const selectedOptionIndex = parseInt(modal.data.selected[0], 10);
                        this.selectedLuggageIndexesObj[index] = selectedOptionIndex;
                        this.luggageCombinationAdd(index, options[selectedOptionIndex], bagType);
                    }

                    if (modalSelectedItem === -1 && (selectedIndex || selectedIndex === 0)) {
                        this.luggageCombinationRemove(index, options[selectedIndex], bagType);
                        delete this.selectedLuggageIndexesObj[index];
                    }
                }
            });
    }

    public removeSelectedLuggage(
        idx: number,
        option: FlightLuggageOption,
        bagType: string,
        selectedIndex: number
    ): void {
        this.passengerDetails[idx].additionalLuggage = TransportLuggagesUtil.removeLuggageCombination(
            this.passengerDetails[idx].additionalLuggage,
            option,
            bagType
        );

        this.listPriceCurrency = option.prices.listPriceCur;

        delete this.selectedLuggageIndexesObj[selectedIndex];
    }

    public openOptionItem(index: number): void {
        this.openedOptionsStringIdx = this.openedOptionsStringIdx + index;
    }

    private setSelectedLuggageIndexes(): void {
        this.bagTypes.map((bagTypeInfo) => {
            this.passengerDetails.map((passenger, index) => {
                this.flightLuggage[bagTypeInfo.type][passenger.type].options.map((bagOption, index2) => {
                    if (
                        this.transportLuggagesUtil.optionPresent(
                            passenger.additionalLuggage,
                            bagTypeInfo.type,
                            bagOption.index
                        )
                    ) {
                        this.selectedLuggageIndexesObj[index] = index2;
                    }
                });
            });
        });
    }

    private setDefaultPassengerDetails(): void {
        this.defaultPassengerDetails = this.setDiffPassengerDetails();
    }

    private setDiffPassengerDetails(): {
        cabin: { bagsCount: number; listPrice: string };
        checkIn: { bagsCount: number; listPrice: string };
    }[] {
        const passengerDetailsArr: {
            cabin: { bagsCount: number; listPrice: string };
            checkIn: { bagsCount: number; listPrice: string };
        }[] = [];

        this.passengerDetails.map((passengerDetails) => {
            let checkInListPrice = '';
            for (const [key, value] of Object.entries(passengerDetails.additionalLuggage.checkIn.options)) {
                checkInListPrice = value.optionPrice.listPrice + '_';
            }

            let cabinListPrice = '';
            for (const [key, value] of Object.entries(passengerDetails.additionalLuggage.cabin.options)) {
                cabinListPrice = value.optionPrice.listPrice + '_';
            }

            passengerDetailsArr.push({
                checkIn: {
                    bagsCount: passengerDetails.additionalLuggage.checkIn.bagsCount,
                    listPrice: checkInListPrice,
                },
                cabin: { bagsCount: passengerDetails.additionalLuggage.cabin.bagsCount, listPrice: cabinListPrice },
            });
        });

        return ToolsService.deepCopy(passengerDetailsArr);
    }
}
