import { Component, Input, ElementRef, ViewChild, OnDestroy, AfterViewInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import {
    StripeElements,
    StripeElementLocale,
    StripeCardNumberElement,
    StripeCardExpiryElement,
    StripeCardCvcElement,
} from '@stripe/stripe-js';

import { StripeService } from '@services/stripe.service';
import { StripeElementsKeys, StripeElementContainer } from '@core/constants/stripe.const';
import { ICart } from '@schemas/modules/cart/cart';
import { TranslateService } from '@ngx-translate/core';
import { SettingsService } from '@services/services/settings-service';
import { Subject } from 'rxjs';
import { skip, takeUntil } from 'rxjs/operators';
import { PlatformService } from '@services/platform.service';

@Component({
    selector: 'tz-stripe',
    templateUrl: './stripe.component.html',
    styleUrls: ['./stripe.component.scss'],
})
export class StripeComponent implements AfterViewInit, OnDestroy {
    @Input() formGroup = new FormGroup({});
    @Input() checkout!: any;
    @Input() cartData!: ICart;
    @ViewChild('stripeFormContainer', { read: ElementRef, static: true }) stripeFormContainer!: ElementRef;

    public stripeForm: {
        [key: string]: {
            el: HTMLElement;
            err: HTMLElement;
            valid: boolean | null;
        };
    } = {};

    private cardNo!: StripeCardNumberElement;
    private cardExp!: StripeCardExpiryElement;
    private cardCvc!: StripeCardCvcElement;
    private destroy$: Subject<boolean> = new Subject<boolean>();

    constructor(
        private stripeService: StripeService,
        private translateService: TranslateService,
        private settingsService: SettingsService,
        public platformService: PlatformService
    ) {}

    ngAfterViewInit(): void {
        this.stripeService.isStripLoaded().subscribe((loaded): void => {
            if (loaded) {
                this.initStripe();

                this.stripeService.updateStripe.pipe(takeUntil(this.destroy$)).subscribe(() => {
                    this.eltBlur(StripeElementContainer.CARDNO);
                    this.eltBlur(StripeElementContainer.CARDEXP);
                    this.eltBlur(StripeElementContainer.CARDCVC);
                });

                this.settingsService.selectedLanguageSubject.pipe(skip(1), takeUntil(this.destroy$)).subscribe(() => {
                    this.initStripe();
                    this.checkout.isStripeValid = false;
                });
            }
        });
    }

    ngOnDestroy() {
        this.cardNo.destroy();
        this.cardExp.destroy();
        this.cardCvc.destroy();

        this.destroy$.next(true);
        this.destroy$.complete();
    }

    private setFormEle(): void {
        const getEl = (id: string): HTMLElement => {
            const nativeElementOfInput: HTMLElement = this.stripeFormContainer.nativeElement.querySelector('#' + id);
            if (nativeElementOfInput) {
                nativeElementOfInput.innerText = '';
                nativeElementOfInput.classList.remove('blured');
                return nativeElementOfInput;
            }

            return getEl(id);
        };

        const setEle = (id: string): void => {
            this.stripeForm[id] = {
                el: getEl(id),
                err: getEl('stripeErr' + id + 'Id'),
                valid: false,
            };
        };

        setEle(StripeElementContainer.CARDNO);
        setEle(StripeElementContainer.CARDEXP);
        setEle(StripeElementContainer.CARDCVC);
    }

    private setStripeValid(): void {
        const isValid = (id: string): boolean | null => this.stripeForm[id].valid;

        this.checkout.isStripeValid =
            isValid(StripeElementContainer.CARDNO) &&
            isValid(StripeElementContainer.CARDEXP) &&
            isValid(StripeElementContainer.CARDCVC);
    }

    private eltBlur(fieldType: string) {
        if (!this.stripeForm[fieldType].valid) {
            this.stripeForm[fieldType].el.classList.add('blured');
            if (this.stripeForm[fieldType].valid === false) {
                this.stripeForm[fieldType].err.textContent = this.translateService.instant('PROFILE.Required');
            }
        } else if (this.stripeForm[fieldType].valid) {
            this.stripeForm[fieldType].el.classList.remove('blured');
        }
        this.setStripeValid();
    }

    private eltChange(fieldType: string, event: any) {
        this.stripeForm[fieldType].err.textContent = '';
        if (event.error) {
            this.stripeForm[fieldType].err.textContent = event.error.message;
            this.stripeForm[fieldType].valid = null;
        } else if (event.complete) {
            this.stripeForm[fieldType].el.classList.remove('blured');
            this.stripeForm[fieldType].valid = true;
        } else if (event.empty) {
            this.stripeForm[fieldType].valid = false;
        }
        this.setStripeValid();
    }

    private eltFocus(fieldType: string) {
        if (!this.stripeForm[fieldType].valid) {
            this.stripeForm[fieldType].el.classList.remove('blured');
        }
    }

    private stripeMount() {
        try {
            const mount = (ele: any, fieldType: string): void => {
                ele.mount('#' + fieldType);

                ele.on('change', (event: any) => this.eltChange(fieldType, event));
                ele.on('blur', () => this.eltBlur(fieldType));
                ele.on('focus', () => this.eltFocus(fieldType));
            };

            mount(this.cardNo, StripeElementContainer.CARDNO);
            mount(this.cardExp, StripeElementContainer.CARDEXP);
            mount(this.cardCvc, StripeElementContainer.CARDCVC);

            console.log('stripe mount successful');
        } catch (e) {
            console.log('stripe mount failed with e', e, 'will retry in 1s');
            setTimeout(() => {
                this.stripeMount.bind(this);
            }, 1000);
        }
    }

    private initStripe() {
        this.setFormEle();

        // Create an instance of Elements
        const elements = this.stripeService.getStripe().elements({
            locale: this.settingsService.langCode as StripeElementLocale,
        }) as StripeElements;

        const stripeStyle = this.platformService.isMobileDevice
            ? this.stripeService.stripeMobileFieldStyle
            : this.stripeService.stripeFieldStyle;
        // Create an instance of the card Element
        this.cardNo = elements.create(StripeElementsKeys.CARDNO, {
            style: stripeStyle,
            showIcon: true,
        });
        this.cardExp = elements.create(StripeElementsKeys.CARDEXP, { style: stripeStyle });
        this.cardCvc = elements.create(StripeElementsKeys.CARDCVC, { style: stripeStyle });
        this.stripeService.setCartElement(this.cardNo);

        // Add an instance of the card Element into the `card-element` <div>
        this.stripeMount();
    }
}
