import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { PlatformService } from '@services/platform.service';
import { TzTravellerService } from '@services/services/tzTraveller.service';

@Component({
    selector: 'tz-checkout-profile',
    templateUrl: './checkout-profile.component.html',
    styleUrls: ['./checkout-profile.component.scss'],
})
export class ProfileInfoComponent implements OnInit {
    @Input() formGroup = new FormGroup({});

    @Output() emailLocked = new EventEmitter<void>();

    public phoneFocused = false;

    constructor(public platformService: PlatformService) {}

    ngOnInit() {}

    public isInValidField(controlName: string): boolean {
        return TzTravellerService.invalidControl(this.formGroup, controlName);
    }
}
