import {
    Component,
    Input,
    Output,
    EventEmitter,
    ChangeDetectionStrategy,
    OnInit,
    ChangeDetectorRef,
} from '@angular/core';
import { Insurance, SelectedInsurance, TzTravelInsurance } from '@schemas/modules/checkout/travel-insurance';
import { UserService } from '@services/modules/user/services/user.service';
import { DateRangePipe } from '@shared/pipes/date-range.pipe';
import { DatePipe } from '@angular/common';
import { PlatformService } from '@services/platform.service';

const defaultInsurance: SelectedInsurance = { selected: {} as Insurance, selectedIdx: -1, validatedTC: false };

@Component({
    selector: 'tz-mob-travel-insurance',
    templateUrl: './mob-travel-insurance.component.html',
    styleUrls: ['./mob-travel-insurance.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class MobTravelInsuranceComponent implements OnInit {
    @Input() tzTravelInsurances!: TzTravelInsurance;
    @Input() selectedInsurance = defaultInsurance;
    @Output() insuranceChange = new EventEmitter<SelectedInsurance>();

    public date!: string | null;

    public insurancesMoreDetailsID: string[] = [];

    constructor(private dateRange: DateRangePipe, private datePipe: DatePipe) {}

    ngOnInit() {
        this.date = this.getDateRange();
    }

    public selectInsuranceOption(option: Insurance | null, selectedIdx = -1) {
        if (option === null) {
            this.insuranceChange.emit(defaultInsurance);
            return;
        }

        this.insuranceChange.emit({
            selected: option,
            selectedIdx,
            validatedTC: false,
        } as SelectedInsurance);
    }

    public getDateRange(): string | null {
        const { start_date, endDate } = this.tzTravelInsurances.parameters;

        return endDate
            ? this.dateRange.transform(start_date, endDate)
            : this.datePipe.transform(start_date, 'mediumDate');
    }

    public sliceInsurances(insurances: string[], formulaRef: string): string[] {
        return this.getIncludeInsurance(formulaRef) ? insurances : insurances.slice(0, 3);
    }

    public insuranceMoreDetails(formulaRef: string): void {
        this.insurancesMoreDetailsID = this.getIncludeInsurance(formulaRef)
            ? this.insurancesMoreDetailsID.filter((id) => id === formulaRef)
            : [...this.insurancesMoreDetailsID, formulaRef];
    }

    public getIncludeInsurance(formulaRef: string): boolean {
        return this.insurancesMoreDetailsID.includes(formulaRef);
    }

    public showMoreDetails(insurance: Insurance): boolean {
        return insurance.content.length > 3 && !this.getIncludeInsurance(insurance.formuleRef);
    }
}
