import { Injectable } from '@angular/core';
import { ApiAccessService } from '@services/searchEngine/services/api-access.service';
import { HttpRequestType } from '@core/constants/http-request-type';
import { BookingReq } from '@schemas/modules/checkout/booking-request';
import { Observable, throwError } from 'rxjs';

@Injectable({
    providedIn: 'root',
})
export class CheckoutDataService extends ApiAccessService {
    public setDraftTravellerInfo(combId: string, req: { data: string }): Observable<any> {
        const params = {
            entity: 'cart',
            id: combId,
            action: 'setDraftTraveller',
            deviceId: this.settingsService.getDeviceID(),
        };

        if (!params.id) {
            return throwError('invalid device id' + params);
        } else {
            return this.apiCallObs(HttpRequestType.POST, params, req);
        }
    }

    public getDraftTravellerInfo(combId: string) {
        const params = {
            entity: 'cart',
            id: combId,
            action: 'getDraftTraveller',
            deviceId: this.settingsService.getDeviceID(),
        };

        if (!params.id) {
            return throwError('invalid data id' + params);
        } else {
            return this.apiCallObs(HttpRequestType.GET, params);
        }
    }

    public getInsurance(bookingReq: BookingReq) {
        const params = {
            entity: 'insurance',
            id: this.settingsService.getDeviceID(),
            actionId: this.settingsService.langCode,
        };

        return this.apiCallObs(HttpRequestType.POST, params, bookingReq);
    }
}
