import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { CartStatus } from '@core/constants/cart-status';
import { ServiceIdToName } from '@core/constants/service-ids-name.const';
import { TranslateService } from '@ngx-translate/core';
import { ICart } from '@schemas/modules/cart/cart';
import { DialogService } from '@services/dialog.service';
import { CartRoutingService } from '@services/modules/cart/services/cart-routing.service';
import { CartService } from '@services/modules/cart/services/cart.service';
import { ServiceId } from '@core/constants/service-id.const';
import { ApiAccessService } from '@services/searchEngine/services/api-access.service';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';
import { SearchMetadataService } from '@services/modules/meta/search-metadata.service';
import { ICartItem } from '@schemas/modules/cart/cart-item';
import { CartUtils } from '@services/modules/cart/utils/cart.utils';

export interface BookingError {
    message: string;
    error: string;
    showError: boolean;
    code: number;
    info?: any;
    reloadRentalMeta: boolean;
}

// Trazler support Booking Error codes
export enum BOOKING_ERROR_TYPE {
    // Payment card error
    INVALID_CARD = 80,
    UNSUPPORTED_CARD = 81,
    // Payment error
    BE_TZ_PAYMENT = 402,
    BE_PAYMENT = 426,
    // price been changed (Reset Content)
    BE_PRICE_CHANGE = 205,
    // Data expired from the provider/from redis
    BE_EXPIRED = 206,
    // Availability changed or ticket sold out while booking
    BE_SOLDOUT = 410,
    // Duplicate booking initiated
    BE_DUPLICATE = 409,
    // Invalid data sent to provider.
    // used when the traveller, customer information is not valid
    BE_VALIDATION = 422,
    // Invalid request sent or request not valid (TZ action)
    BE_INVALIDRQ = 400,
    // Invalid response received (TZ action)
    BE_INVALIDRS = 406,
    // Internal system error (Tz & provider)
    BE_INTERNAL = 500,
    // BENotAllowed - access restricted from the server
    BE_NOT_ALLOWED = 405,
    // If system unable to detect error.
    // use this (watch unknown error in logs and add it to error list)
    BE_UNKNOWN = 421,
    // Server timeout error
    BE_TIMEOUT = 408,
}

@Injectable({
    providedIn: 'root',
})
export class CheckoutErrorService {
    constructor(
        private router: Router,
        private translateService: TranslateService,
        private dialogService: DialogService,
        private cartService: CartService,
        private cartRoutingService: CartRoutingService,
        private apiAccessService: ApiAccessService,
        private searchDataService: SearchDataService,
        private searchMetadataService: SearchMetadataService
    ) {}
    private static errorType(errorCode: number) {
        switch (errorCode) {
            case BOOKING_ERROR_TYPE.BE_SOLDOUT:
                return 7.2;
            case BOOKING_ERROR_TYPE.BE_PRICE_CHANGE:
                return 7.3;
            default:
                return 7.1;
        }
    }

    private static roomsFailed(items: ICartItem[]): boolean {
        const hasBookedRooms = items.some(
            (item) =>
                item.serviceId === ServiceId.ACC &&
                item.selectedItem.bookingOptions.some((option) => CartUtils.isFailed(option.status))
        );

        const hasFailedRooms = items.some(
            (item) =>
                item.serviceId === ServiceId.ACC &&
                item.selectedItem.bookingOptions.some((option) => CartUtils.isBooked(option.status))
        );

        return hasBookedRooms && hasFailedRooms;
    }

    public handleBookingError(error: BookingError, fromStripe: boolean): void {
        const paymentError =
            error.code === BOOKING_ERROR_TYPE.INVALID_CARD ||
            error.code === BOOKING_ERROR_TYPE.UNSUPPORTED_CARD ||
            error.code === BOOKING_ERROR_TYPE.BE_TZ_PAYMENT ||
            error.code === BOOKING_ERROR_TYPE.BE_PAYMENT;
        const isCheckoutError =
            error.code === BOOKING_ERROR_TYPE.BE_VALIDATION || error.code === BOOKING_ERROR_TYPE.BE_INVALIDRQ;

        if (paymentError || isCheckoutError) {
            if (paymentError) {
                this.router.navigate(['checkout-processed', 'payment-failed']);
            } else {
                this.dialogService.openSnackBar({ message: this.formCheckoutErrorMessage(error, fromStripe) });
            }
            return;
        }
        if (error.reloadRentalMeta) {
            console.log('reload rental meta data');
            this.apiAccessService
                .getMetaRes(
                    this.searchDataService.getCombId(),
                    ServiceId.CAR_RENTAL,
                    this.searchDataService.getDataId(ServiceId.CAR_RENTAL)
                )
                .then(
                    (res: any) => {
                        this.searchMetadataService.saveSearch(res);
                        this.cartRoutingService.moveToSummary().then(() => this.showInvalidCartAlert());
                    },
                    (failure: any) => {
                        console.error(failure);
                        this.cartRoutingService.moveToSummary().then(() => this.showInvalidCartAlert());
                    }
                );
        } else {
            this.cartRoutingService.moveToSummary().then(() => this.showInvalidCartAlert());
        }
    }

    private formCheckoutErrorMessage(error: BookingError, fromStripe: boolean): string {
        let message = error.message;
        switch (error.code) {
            case BOOKING_ERROR_TYPE.INVALID_CARD:
                message = this.translateService.instant('NOTIFS.ERROR.CREDIT_CARDS_INVALID_CARD_NUMBER');
                break;
            case BOOKING_ERROR_TYPE.UNSUPPORTED_CARD:
                message = this.translateService.instant('NOTIFS.ERROR.CREDIT_CARDS_UNSUPPORTED');
                break;
            case BOOKING_ERROR_TYPE.BE_TZ_PAYMENT:
            case BOOKING_ERROR_TYPE.BE_PAYMENT:
                message = this.translateService.instant('NOTIFS.ERROR.PAYMENT_FAIL');
                break;
            case BOOKING_ERROR_TYPE.BE_VALIDATION:
            case BOOKING_ERROR_TYPE.BE_INVALIDRQ:
                message = this.translateService.instant('NOTIFS.ERROR.INVALID_REQ');
                break;
        }
        return message;
    }

    private showInvalidCartAlert(): void {
        const cartData = this.cartService.getCartData() as ICart;
        const errorType = CheckoutErrorService.errorType(cartData.errorCode);

        if (CheckoutErrorService.roomsFailed(cartData.items)) {
            this.dialogService.openConfirmationDialog({
                title: this.translateService.instant('BOOKINGS.REJECTION.ROOM_REJECTION_POPUPS.TITLE_' + errorType),
                content: this.translateService.instant('BOOKINGS.REJECTION.ROOM_REJECTION_POPUPS.MESSAGE_' + errorType),
                confirmBtnTitle: this.translateService.instant('GENERAL.OK'),
            });

            return;
        }

        let invalidItems = cartData.items.filter((item) =>
            item.selectedItem.bookingOptions.some((option: any) => option.status >= CartStatus.STATUS_REJECTED)
        );
        invalidItems = invalidItems.filter(
            (item, i, items) => items.findIndex((t) => t.serviceId === item.serviceId) === i
        );

        if (invalidItems.length === 0) {
            return;
        }

        const serviceName = invalidItems.reduce(
            (servicesTxt, item) =>
                servicesTxt +
                (servicesTxt !== '' ? ', ' : '') +
                this.translateService.instant('SERVICES.' + item.serviceId).toLowerCase(),
            ''
        );

        this.dialogService.openConfirmationDialog({
            title: this.translateService.instant('BOOKINGS.REJECTION.POPUPS.TITLE_' + errorType, {
                service: serviceName,
            }),
            content: this.translateService.instant('BOOKINGS.REJECTION.POPUPS.MESSAGE_' + errorType, {
                service: serviceName,
            }),
            confirmBtnTitle: this.translateService.instant('GENERAL.OK'),
        });
    }
}
