import { Component, Input, OnChanges, Output, SimpleChanges, EventEmitter } from '@angular/core';
import { FormArray } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { CheckoutTraveller } from '@schemas/modules/checkout/cart-traveller';
import {
    CheckoutLuggages,
    FlightBaggage,
    SelectedLuggageAmount,
    TrainTripLuggage,
} from '@schemas/modules/checkout/transport-luggage';
import { ModalService } from '@services/modal.service';
import { MobAdditionalLuggagesComponent } from '../mob-additional-luggages/mob-additional-luggages.component';

@Component({
    selector: 'tz-mob-transport-luggages',
    templateUrl: './mob-transport-luggages.component.html',
    styleUrls: ['./mob-transport-luggages.component.scss'],
})
export class MobTransportLuggagesComponent implements OnChanges {
    @Input() checkoutLuggages!: CheckoutLuggages;
    @Input() checkout: any;
    @Input() passengerDetails!: CheckoutTraveller[];
    @Input() transportFormArray!: FormArray;

    @Output() updateHelper: EventEmitter<void> = new EventEmitter<void>();

    public flightLuggage!: FlightBaggage;
    public trainLuggage: { tripType: string; tripLuggages: TrainTripLuggage[] }[] = [];
    public luggagesIncluded = false;

    constructor(private translateService: TranslateService, private modalService: ModalService) {}

    ngOnChanges(changes: SimpleChanges): void {
        if (changes['checkoutLuggages'] && this.checkoutLuggages) {
            this.checkLuggagesIncluded();
            if (this.checkoutLuggages.flight) {
                this.flightLuggage = this.checkoutLuggages.flight.flightLuggage;
            } else if (this.checkoutLuggages.train) {
                const luggage = this.checkoutLuggages.train;
                this.trainLuggage = luggage.isRoundTripLuggage
                    ? [
                          {
                              tripType: 'onwardLuggage',
                              tripLuggages: luggage.onwardLuggage,
                          },
                      ]
                    : [
                          {
                              tripType: 'onwardLuggage',
                              tripLuggages: luggage.onwardLuggage,
                          },
                          {
                              tripType: 'returnLuggage',
                              tripLuggages: luggage.returnLuggage,
                          },
                      ];
            }
        }
    }

    public getTravellerName(idx: number): string {
        const formvalue = this.transportFormArray.controls[idx].value;
        this.passengerDetails[idx].firstName = formvalue.firstName;
        this.passengerDetails[idx].lastName = formvalue.lastName;

        if (!formvalue.firstName || !formvalue.lastName) {
            if (idx === 0) {
                return this.translateService.instant('TRAVELLER.PRIMARY_TRAVELER');
            } else {
                return `${this.translateService.instant('TRAVELLER.TRAVELER')} ${idx + 1}`;
            }
        }
        return `${formvalue.firstName} ${formvalue.middleName ? formvalue.middleName : ''} ${formvalue.lastName}`;
    }

    public addLuggages(): void {
        this.modalService
            .createDetailsPage(MobAdditionalLuggagesComponent, {
                data: {
                    flightLuggages: this.checkoutLuggages.flight.flightLuggage,
                    checkout: this.checkout,
                    passengerDetails: this.passengerDetails,
                    headerModalType: 'default',
                },
                title: `${this.translateService.instant('FLIGHT.LUGGAGE.ADD_BAGGAGE')} (${this.translateService.instant(
                    this.checkoutLuggages.flight.isRoundTrip ? 'GENERAL.Round_trip' : 'GENERAL.One_way'
                )})`,
            })
            .then((modal) => {
                if (!modal.data || !modal.data.passengerDetails) {
                    return;
                }

                let total = 0;
                const selectedData: {
                    passengerDetails: CheckoutTraveller[];
                    listPriceCurrency: string;
                } = modal.data;
                for (const passenger of selectedData.passengerDetails) {
                    total += passenger.additionalLuggage.totalPrice;
                }

                this.checkout.baggageSelected = total > 0;
                this.checkout.additionalLuggagePrice = {
                    listPrice: total,
                    listPriceCur: selectedData.listPriceCurrency,
                } as SelectedLuggageAmount;
                this.updateHelper.emit();
            });
    }

    private checkLuggagesIncluded(): void {
        this.luggagesIncluded =
            (this.checkoutLuggages.flight &&
                !!(
                    this.checkoutLuggages.flight.flightLuggage.includedOptions?.bag1 ||
                    this.checkoutLuggages.flight.flightLuggage.includedOptions?.bag2
                )) ||
            (this.checkoutLuggages.train &&
                (this.checkoutLuggages.train.onwardLuggage.length > 0 ||
                    this.checkoutLuggages.train.returnLuggage.length > 0));
    }
}
