import { Component, Input, OnChanges, Output, SimpleChanges, EventEmitter } from '@angular/core';
import { FormArray } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { CheckoutTraveller } from '@schemas/modules/checkout/cart-traveller';
import {
    CheckoutLuggages,
    FlightBaggage,
    SelectedLuggageAmount,
    TrainTripLuggage,
} from '@schemas/modules/checkout/transport-luggage';
import { FlightLuggageOption, FlightLuggageOptionTypes } from '@schemas/modules/flights/flight-luggage.model';
import { TransportLuggagesUtil } from '@services/modules/checkout/utils/transport-luggages.utils';
import { TzCurrencyPipe } from '@shared/pipes/tz-currency.pipe';

@Component({
    selector: 'tz-transport-luggages',
    templateUrl: './transport-luggages.component.html',
    styleUrls: ['./transport-luggages.component.scss'],
})
export class TransportLuggagesComponent implements OnChanges {
    @Input() checkoutLuggages!: CheckoutLuggages;
    @Input() checkout: any;
    @Input() passengerDetails!: CheckoutTraveller[];
    @Input() transportFormArray!: FormArray;

    @Output() updateHelper: EventEmitter<void> = new EventEmitter<void>();

    public flightLuggage!: FlightBaggage;
    public trainLuggage: { tripType: string; tripLuggages: TrainTripLuggage[] }[] = [];
    public showAdditionalLuggage = false;
    public bagTypes!: {
        txt: string;
        type: FlightLuggageOptionTypes.CABIN | FlightLuggageOptionTypes.CHECKIN;
    }[];
    public transportLuggagesUtil = TransportLuggagesUtil;

    private listPriceCurrency!: string;

    constructor(public translateService: TranslateService, private tzCurrencyPipe: TzCurrencyPipe) {}

    get luggagesIncluded(): boolean {
        if (this.checkoutLuggages.flight) {
            return !!(this.flightLuggage.includedOptions?.bag1 || this.flightLuggage.includedOptions?.bag2);
        }

        return (
            this.checkoutLuggages.train.onwardLuggage.length > 0 || this.checkoutLuggages.train.returnLuggage.length > 0
        );
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes['checkoutLuggages'] && this.checkoutLuggages) {
            if (this.checkoutLuggages.flight) {
                this.flightLuggage = this.checkoutLuggages.flight.flightLuggage;

                this.bagTypes = [
                    {
                        txt:
                            this.flightLuggage.checkIn && this.flightLuggage.checkIn.replaceIncluded
                                ? 'ADD_PASSENGER.Checked_bag_add'
                                : 'ADD_PASSENGER.Checked_bag',
                        type: FlightLuggageOptionTypes.CHECKIN,
                    },
                    /*{
                        //buying cabin bag is disabled
                        txt:
                            this.flightLuggage.cabin && this.flightLuggage.cabin.replaceIncluded
                                ? 'ADD_PASSENGER.Cabin_bag_add'
                                : 'ADD_PASSENGER.Cabin_bag',
                        type: FlightLuggageOptionTypes.CABIN,
                    },*/
                ];
            } else if (this.checkoutLuggages.train) {
                const luggage = this.checkoutLuggages.train;
                this.trainLuggage = luggage.isRoundTripLuggage
                    ? [
                          {
                              tripType: 'onwardLuggage',
                              tripLuggages: luggage.onwardLuggage,
                          },
                      ]
                    : [
                          {
                              tripType: 'onwardLuggage',
                              tripLuggages: luggage.onwardLuggage,
                          },
                          {
                              tripType: 'returnLuggage',
                              tripLuggages: luggage.returnLuggage,
                          },
                      ];
            }
        }
    }

    public getTravellerName(idx: number): string {
        return TransportLuggagesUtil.getTravellerName(
            this.transportFormArray.controls[idx].value,
            idx,
            this.translateService
        );
    }

    public luggageCombinationAdd(idx: number, option: FlightLuggageOption, bagType: string): void {
        this.passengerDetails[idx].additionalLuggage = TransportLuggagesUtil.formLuggageCombination(
            this.passengerDetails[idx].additionalLuggage,
            option,
            bagType
        );

        this.listPriceCurrency = option.prices.listPriceCur;
        this.updateLuggagePrice();
    }

    public luggageCombinationRemove(idx: number, option: FlightLuggageOption, bagType: string): void {
        this.passengerDetails[idx].additionalLuggage = TransportLuggagesUtil.removeLuggageCombination(
            this.passengerDetails[idx].additionalLuggage,
            option,
            bagType
        );

        this.listPriceCurrency = option.prices.listPriceCur;
        this.updateLuggagePrice();
    }

    public selectLuggageOption(idx: number, option: FlightLuggageOption | null, bagType: string): void {
        this.passengerDetails[idx].additionalLuggage = TransportLuggagesUtil.selectLuggageOption(
            this.passengerDetails[idx].additionalLuggage,
            option,
            bagType,
            this.tzCurrencyPipe
        );

        this.updateLuggagePrice();
    }

    private updateLuggagePrice() {
        let total = 0;
        for (const passenger of this.passengerDetails) {
            total += passenger.additionalLuggage.totalPrice;
        }

        this.checkout.baggageSelected = total > 0;
        this.checkout.additionalLuggagePrice = {
            listPrice: total,
            listPriceCur: this.listPriceCurrency,
        } as SelectedLuggageAmount;
        this.updateHelper.emit();
    }
}
