import { Component, Input } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { CartStatus } from '@core/constants/cart-status';
import { TranslateService } from '@ngx-translate/core';
import { ICart } from '@schemas/modules/cart/cart';
import { BookingReq } from '@schemas/modules/checkout/booking-request';
import { DialogService } from '@services/dialog.service';
import { CartService } from '@services/modules/cart/services/cart.service';
import { CheckoutErrorService } from '@services/modules/checkout/services/checkout-error.service';
import { TzCheckoutService } from '@services/modules/checkout/services/tz-checkout.service';
import { LoaderService } from '@services/modules/core/loader.service';
import { ScrollService } from '@services/modules/core/scroll-service';
import { PlatformService } from '@services/platform.service';
import { ApiAccessService } from '@services/searchEngine/services/api-access.service';
import { StripeService } from '@services/stripe.service';
import { Observable } from 'rxjs';
import { TzServiceTabs } from '../../../results/tabsUtils/tz-tabs-service';

@Component({
    selector: 'tz-checkout-payment',
    templateUrl: './process-payment.component.html',
    styleUrls: ['./process-payment.component.scss'],
})
export class PaymentComponent {
    @Input() paymentForm: FormGroup = new FormGroup({});
    @Input() checkout!: any;
    @Input() validTravellers = false;
    @Input() cartData!: ICart;

    constructor(
        private tzCheckoutService: TzCheckoutService,
        private cartService: CartService,
        private trazlerAPI: ApiAccessService,
        private stripeService: StripeService,
        private dialogService: DialogService,
        private translateService: TranslateService,
        private checkoutErrorService: CheckoutErrorService,
        private router: Router,
        private loaderService: LoaderService,
        public platformService: PlatformService,
        private scrollService: ScrollService,
        private serviceTabs: TzServiceTabs
    ) {}

    get billingFormGroup(): FormGroup {
        return this.paymentForm?.get('billing') as FormGroup;
    }

    get disableBooking(): boolean {
        const stripeForm = this.paymentForm?.get('payment') as FormGroup;
        return (
            /*!this.checkout.agreeToTerms ||
            !this.checkout.healthDeclarationChecked ||*/
            !this.paymentForm.valid || (stripeForm && !this.checkout.isStripeValid) || !this.validTravellers
        );
    }

    get showTripRefundInfo(): boolean {
        if (!this.cartData || this.cartData.items.length <= 1) {
            return false;
        }

        const cartItems = [...new Map(this.cartData.items.map((item) => [item.serviceId, item])).values()];
        return cartItems.length > 1;
    }

    public updateValidation(): void {
        this.tzCheckoutService.setBillingValidators(this.billingFormGroup, this.checkout.invoiceRequired);
    }

    public proceedCheckout(): void {
        if (this.disableBooking) {
            this.checkFormValidity();
            return;
        }

        const bookingReq = this.tzCheckoutService.makeBookingReq(this.cartData.items, this.checkout, this.paymentForm);
        console.log('Booking bookingReq', bookingReq);
        // LocalStorageManager.removeItem('_prevState');

        this.trazlerAPI.Booking(bookingReq).then(
            (success: any) => {
                // LocalStorageManager.setItem('latestBookingId', success.id);
                this.handleBookingSuccess(success, bookingReq);
            },
            (failure: any) => {
                // LocalStorageManager.removeItem('latestBookingId');
                this.bookingFailure(failure, false, bookingReq);
            }
        );
    }

    private bookingSuccess(bookingReq: BookingReq, success?: any) {
        console.log('success: ' + success);
        this.showLoader(false);
        if (success.infoMessage) {
            this.showPriceChangeAlert(success.infoMessage).subscribe(() => {
                this.dialogService.openSnackBar({
                    message: this.translateService.instant('NOTIFS.BOOKING_SUCCESS', { bookingId: success?.id }),
                });
                this.router.navigate(['checkout-processed', 'success', success.id]);
            });

            return;
        }

        this.router.navigate(['checkout-processed', 'success', success.id]);
    }

    // The card action has been handled
    // The PaymentIntent can be confirmed again on the server
    private makeBooking(bookingReq: BookingReq) {
        console.log(bookingReq);
        this.trazlerAPI.Booking(bookingReq).then(
            (res: any) => {
                console.log('Booking res After card action handled: ', res);
                // LocalStorageManager.setItem('latestBookingId', res.id);
                this.bookingSuccess(bookingReq, res);
            },
            (err: any) => {
                this.bookingFailure(err, false, bookingReq);
                console.log(err);
            }
        );
    }

    //Handle payment for no PCI
    private hdlNoPCIPayment(bookingReq: BookingReq, clientSecret: string) {
        this.stripeService
            .getStripe()
            .confirmCardPayment(clientSecret, {
                payment_method: {
                    card: this.stripeService.getCartElement(),
                },
            })
            .then((result: any) => {
                if (result.error) {
                    this.showLoader(false);
                    this.dialogService.openSnackBar({ message: result.error.message });
                    this.router.navigate(['checkout-processed', 'payment-failed']);
                    return;
                } else {
                    bookingReq.pId = result.paymentIntent.id;
                    this.makeBooking(bookingReq);
                }
            });
    }

    //Handle card payment with client secret
    private hdlPaymentWithSecret(bookingReq: BookingReq, clientSecret: string): void {
        this.showLoader(true, 'LOADER_MESSAGES.BOOKING_CONFIRM');
        this.stripeService
            .getStripe()
            .confirmCardPayment(clientSecret)
            .then((result: any) => {
                if (result.error) {
                    this.bookingFailure(result.error, true, bookingReq);
                } else {
                    bookingReq.pId = result.paymentIntent.id;
                    this.makeBooking(bookingReq);
                }
            });
    }

    private handleBookingSuccess(successData: any, bookingReq: BookingReq): void {
        console.log('Booking success: ', successData);
        if (successData && successData.stripeAction === 1) {
            this.hdlNoPCIPayment(bookingReq, successData.clientSecret);
        } else if (successData && successData.clientSecret) {
            this.hdlPaymentWithSecret(bookingReq, successData.clientSecret);
        } else {
            this.bookingSuccess(bookingReq, successData);
        }
    }

    private bookingFailure(failure: any, isStripe: boolean, bookingReq: BookingReq): void {
        console.log('booking failure: ', failure);
        this.cartService.reInit().then((cartData: ICart) => {
            console.log('cartData after failure is:', cartData);
            if (cartData.bookingStatus === CartStatus.STATUS_PROGRESS) {
                this.showLoader(true, 'LOADER_MESSAGES.BOOKING_IN_PROGRESS');
                const combId = cartData.combId;
                const deviceId = cartData.deviceId;
                let retry = 5; // retry 5 times to get a final booking status
                // Here in this case we wait for the booking to confirmed from provider
                const recheckInterval = setInterval(() => {
                    this.cartService.recheckCartItem(combId, deviceId, true).then(
                        (cartRes: ICart) => {
                            retry--;
                            if (cartRes.bookingStatus === CartStatus.STATUS_PROGRESS) {
                                if (!retry) {
                                    clearInterval(recheckInterval);
                                    this.showLoader(false);
                                }
                                return;
                            }

                            if (cartRes.bookingStatus === CartStatus.STATUS_PRICE_CHANGED) {
                                clearInterval(recheckInterval);
                                this.bookingSuccess(bookingReq, cartRes);
                            }
                            if (!retry) {
                                clearInterval(recheckInterval);
                                this.showLoader(false);
                            }
                        },
                        (cartErr: any) => {
                            if (cartErr.error.error === 'redis: nil') {
                                clearInterval(recheckInterval);
                                this.bookingSuccess(bookingReq);
                            }
                        }
                    );
                }, 10000);

                return;
            }

            this.showLoader(false);
            if (failure.error && failure.error.info) {
                this.showPriceChangeAlert(failure.error.info).subscribe(() => {
                    this.checkoutErrorService.handleBookingError(failure.error, isStripe);
                });

                return;
            }

            this.checkoutErrorService.handleBookingError(isStripe ? failure : failure.error, isStripe);
        });
    }

    private showPriceChangeAlert(infoContent: { title: string; content: string }): Observable<boolean> {
        return this.dialogService
            .openConfirmationDialog({
                title: this.translateService.instant(infoContent.title),
                content: infoContent.content,
                confirmBtnTitle: this.translateService.instant('CHECKOUT.PAYMENT.OK_GREAT'),
            })
            .afterClosed();
    }

    private showLoader(show: boolean, msg?: string, title?: string) {
        this.loaderService.triggerLoading.emit({
            loading: show,
            type: 'small',
            textKey: msg,
            title,
        });
    }

    private checkFormValidity(): void {
        let invalidId = '';
        const markFormAsDirty = (formGroup: FormGroup, formId: string) => {
            Object.keys(formGroup.controls).forEach((key) => {
                formGroup.controls[key].markAsDirty();
                formGroup.controls[key].markAsTouched();
            });

            if (invalidId === '') {
                invalidId = formId;
            }
        };

        const profileForm = this.paymentForm?.get('profile') as FormGroup;
        if (profileForm.invalid) {
            markFormAsDirty(profileForm, 'PAYMENT_PROFILE');
        }

        const paymentForm = this.paymentForm?.get('payment') as FormGroup;
        if (paymentForm?.invalid || (paymentForm && !this.checkout.isStripeValid)) {
            markFormAsDirty(paymentForm, 'PAYMENT_STRIPE');
            this.stripeService.updateStripe.next();
        }

        if (this.checkout.invoiceRequired && this.billingFormGroup.invalid) {
            markFormAsDirty(this.billingFormGroup, 'PAYMENT_BILLING');
        }

        if (invalidId !== '') {
            this.scrollService.scrollTo(invalidId, 70, true);
        }
    }
}
