import {
    ChangeDetectionStrategy,
    Component,
    EventEmitter,
    Input,
    Output,
    OnInit,
    OnDestroy,
    ChangeDetectorRef,
} from '@angular/core';
import { FormGroup } from '@angular/forms';
import * as moment from 'moment';

import { TravellerType } from '@core/constants/traveller-type';
import { Passenger } from '@shared/schemas/tz-customer-info';
import { DatesUtils } from '@core/utils/dates-utils';
import { UserService } from '@services/modules/user/services/user.service';
import { CheckoutTraveller } from '@schemas/modules/checkout/cart-traveller';
import { TzDatePipe } from '@shared/pipes/tz-date.pipe';
import { Subject, takeUntil } from 'rxjs';
import { SettingsService } from '@services/services/settings-service';

@Component({
    selector: 'tz-profile-travellers',
    templateUrl: './profile-travellers.component.html',
    styleUrls: ['./profile-travellers.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ProfileTravellersComponent implements OnInit, OnDestroy {
    @Input() formGroup = new FormGroup({});
    @Input() ageAt = new Date();
    @Input() currentTraveller!: CheckoutTraveller;
    @Input() checkoutTravllers?: CheckoutTraveller[];
    @Input() transportTravllers: CheckoutTraveller[] = [];
    @Input() isTransport = false;
    @Input() prefixText = '';

    @Output() resetTraveller: EventEmitter<void> = new EventEmitter<void>();
    @Output() setTraveller = new EventEmitter<{ traveller: Passenger }>();

    public dateUtils = DatesUtils;

    private destroy$: Subject<boolean> = new Subject<boolean>();

    constructor(
        private userService: UserService,
        private datePipe: TzDatePipe,
        private readonly changeDetectorRef: ChangeDetectorRef,
        private settingsService: SettingsService
    ) {}

    get travelerName(): string {
        const traveller = this.formGroup;
        return (
            traveller.value.firstName +
            ' ' +
            (traveller.value.middleName ? traveller.value.middleName : '') +
            ' ' +
            traveller.value.lastName
        );
    }

    get travelerDOB(): string {
        const dob = moment(this.currentTraveller.dateOfBirth, DatesUtils.updateTzTravelerFormat)?.toDate();
        return this.datePipe.transform(dob, 'mediumDate') ?? '';
    }

    get profileTravellers(): Passenger[] {
        const travellers: Passenger[] = [];
        const selectedAge = this.currentTraveller.selectedAge;

        if (this.transportTravllers && this.transportTravllers.length > 0) {
            let idx = 0;
            for (const traveller of this.transportTravllers) {
                if (traveller.id && traveller.id !== '') {
                    continue;
                }

                const dob = moment(traveller.dateOfBirth, DatesUtils.updateTzTravelerFormat)?.toDate();
                const age = this.getTravellerAge(dob);
                const isValidTraveller =
                    this.currentTraveller.type === TravellerType.ADULT &&
                    (selectedAge && selectedAge !== -1 ? selectedAge === age : age >= 18);

                if (isValidTraveller) {
                    travellers.push({
                        ...traveller,
                        id: `transport${idx}`,
                        recent: true,
                        dateOfBirth: moment(dob).format(DatesUtils.savedTzTravelerFormat),
                    });

                    idx++;
                }
            }
        }

        const customer = this.userService.getCustomerInformation();
        if (!this.userService.IsLogged() || !customer || !customer.passengers) {
            return travellers;
        }

        for (const passenger of customer.passengers) {
            if (this.checkoutTravllers) {
                passenger.disable = this.checkoutTravllers.some((t: Passenger) => passenger.id === t.id);
            }

            const dob = moment(passenger.dateOfBirth, DatesUtils.savedTzTravelerFormat)?.toDate();
            const age = this.getTravellerAge(dob);
            if (
                this.currentTraveller.type === TravellerType.ADULT &&
                (selectedAge && selectedAge !== -1 ? selectedAge === age : age >= 18)
            ) {
                travellers.push(passenger);
            }
            if (
                this.currentTraveller.type === TravellerType.CHILD &&
                (selectedAge && selectedAge !== -1 ? selectedAge === age : age < 18 && age >= 2)
            ) {
                travellers.push(passenger);
            }
            if (this.currentTraveller.type === TravellerType.INFANT && age < 2) {
                travellers.push(passenger);
            }
        }

        return travellers;
    }

    ngOnInit(): void {
        this.userService.loggedIn$.pipe(takeUntil(this.destroy$)).subscribe((isLogged) => {
            this.changeDetectorRef.detectChanges();
        });
    }

    ngOnDestroy(): void {
        this.destroy$.next(true);
        this.destroy$.complete();
    }

    public setValue(item: Passenger): void {
        this.setTraveller.emit({
            traveller: item,
        });
    }

    public reset(): void {
        this.resetTraveller.emit();
    }

    public bannerText(): string {
        const formGroup = this.formGroup;
        return this.isTransport ? this.transportText(formGroup) : this.getTravellerName(formGroup);
    }

    public formatTravelerDate(dateOfBirth: Date | string): string {
        const travelerDate = this.settingsService.locales.DATETIME_FORMATS.tzTravelerShortDate;
        return DatesUtils.formatDate(
            dateOfBirth,
            DatesUtils.savedTzTravelerFormat,
            travelerDate.replace(new RegExp('/', 'g'), '.')
        );
    }

    private transportText(traveller: FormGroup): string {
        return traveller.valid ? ` | ${this.travelerName} | ${this.travelerDOB}` : '';
    }

    private getTravellerName(traveller: FormGroup): string {
        return traveller.valid ? ` | ${traveller.value.firstName} ${traveller.value.lastName}` : '';
    }

    private getTravellerAge(dateOfBirth: Date): number {
        return DatesUtils.getAgeDiff(dateOfBirth, this.ageAt ? this.ageAt : new Date());
    }
}
