import { Component, HostListener, OnDestroy, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup } from '@angular/forms';
import { CartService } from '@services/modules/cart/services/cart.service';
import { ICart } from '@schemas/modules/cart/cart';
import { TzCheckoutService } from '@services/modules/checkout/services/tz-checkout.service';
import { CartTravellerInfo, CheckoutTraveller } from '@schemas/modules/checkout/cart-traveller';
import { TzTravellerService } from '@services/services/tzTraveller.service';
import { TravellerType } from '@core/constants/traveller-type';
import { CheckoutLuggages, SelectedLuggageAmount } from '@schemas/modules/checkout/transport-luggage';
import { CheckoutCartServiceSummary } from '@schemas/modules/checkout/cart-summary';
import { SettingsService } from '@services/services/settings-service';
import { skip, takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';
import { StorageManagerUtil } from '@core/utils/storage-manager.util';
import { CheckoutDataService } from '@services/modules/checkout/dataservices/checkout.data.service';
import { Insurance, SelectedInsurance, TzTravelInsurance } from '@schemas/modules/checkout/travel-insurance';
import { UserService } from '@services/modules/user/services/user.service';
import { ScrollService } from '@services/modules/core/scroll-service';
import { CartRoutingService } from '@services/modules/cart/services/cart-routing.service';
import { LoaderService } from '@services/modules/core/loader.service';
import { DialogService } from '@services/dialog.service';
import { TranslateService } from '@ngx-translate/core';
import { PlatformService } from '@services/platform.service';
import { ServiceId } from '@core/constants/service-id.const';

@Component({
    selector: 'tz-checkout',
    templateUrl: './checkout.component.html',
    styleUrls: ['./checkout.component.scss', './checkout.component.mobile.scss'],
})
export class CheckoutComponent implements OnInit, OnDestroy {
    public checkout: any = {
        isStripeValid: false,
        invoiceRequired: false,
        agreeToTerms: false,
        healthDeclarationChecked: false,
        baggageSelected: false,
        insurance: { selected: {} as Insurance, selectedIdx: null, validatedTC: false } as SelectedInsurance,
        additionalLuggagePrice: { listPrice: 0, listPriceCur: '' } as SelectedLuggageAmount,
        transportID: '',
    };

    public travellersForm = new FormGroup({});
    public paymentForm = new FormGroup({});
    public cartInfo!: CartTravellerInfo;
    public checkoutLuggages!: CheckoutLuggages;
    public cartSummary: CheckoutCartServiceSummary[] = [];
    public cartData!: ICart;
    public tzTravelInsurances!: TzTravelInsurance;
    public activeTab: 0 | 1 = 0; // 0: travellers tab, 1: payment tab
    public summaryExpanded = false;
    public insuranceTouched = false;

    private destroy$: Subject<boolean> = new Subject<boolean>();

    constructor(
        private cartService: CartService,
        private fb: FormBuilder,
        private tzTravellerService: TzTravellerService,
        private tzCheckoutService: TzCheckoutService,
        private cartRoutingService: CartRoutingService,
        private settingsService: SettingsService,
        private searchDataService: SearchDataService,
        private checkoutDataService: CheckoutDataService,
        private userService: UserService,
        private scrollService: ScrollService,
        private loaderService: LoaderService,
        private dialogService: DialogService,
        private translateService: TranslateService,
        public platformService: PlatformService
    ) {}

    get profileFormGroup(): FormGroup {
        return this.paymentForm?.get('profile') as FormGroup;
    }

    get paymentFormGroup(): FormGroup {
        return this.paymentForm?.get('payment') as FormGroup;
    }

    get transportFormArray(): FormArray {
        return this.travellersForm.controls['transport'] as FormArray;
    }

    get insuranceSelected(): boolean {
        return (
            !this.tzTravelInsurances ||
            this.tzTravelInsurances?.insurances.length === 0 ||
            (this.tzTravelInsurances?.insurances.length > 0 && this.checkout.insurance.selectedIdx !== null)
        );
    }

    private get hotelFormArray(): FormArray {
        return this.travellersForm.controls['hotel'] as FormArray;
    }

    @HostListener('window:beforeunload')
    beforeUnload() {
        this.draftTravellerInfo();
    }

    ngOnInit() {
        this.scrollService.scrollToTop();
        this.initializeCheckoutComponent();
    }

    ngOnDestroy(): void {
        this.draftTravellerInfo();
        this.destroy$.next(true);
        this.destroy$.complete();
    }

    public updateHelper() {
        this.cartData = {
            ...this.cartData,
            helpers: this.tzCheckoutService.getHelpers(
                this.cartData,
                this.checkout.additionalLuggagePrice,
                this.checkout.insurance.selected
            ),
        };
    }

    public switchTab(tabId: 0 | 1): void {
        if (tabId === 1) {
            if (this.travellersForm.invalid || !this.insuranceSelected) {
                this.scrollService.scrollTo(this.onMarkFormAsVisited(), 70, true);
                return;
            } else if (this.cartInfo.FLIGHT_TRAIN && this.userService.IsLogged()) {
                for (const traveller of this.checkout.passengerDetails as CheckoutTraveller[]) {
                    if (
                        traveller.id !== undefined &&
                        traveller.id !== '' &&
                        traveller.updateTraveller &&
                        traveller.travellerUpdated
                    ) {
                        const updateInfo = traveller.docNotRequired
                            ? this.tzTravellerService.formProfileTraveller(traveller)
                            : traveller;
                        this.tzTravellerService.updateTraveller(updateInfo);
                        traveller.travellerUpdated = false;
                    } else if ((traveller.id === undefined || traveller.id === '') && traveller.saveTraveller) {
                        this.tzTravellerService.saveTraveller(traveller);
                    }
                }
            }

            if (this.cartInfo.FLIGHT_TRAIN) {
                const selctedInsurance = this.checkout.insurance;
                const hasSelectedOption = selctedInsurance.selectedIdx !== null && selctedInsurance.selectedIdx !== -1;
                if (hasSelectedOption) {
                    this.getTravelInsurance(true);
                    return;
                }

                this.getTravelInsurance();
            }
        }

        this.goto(tabId);
    }

    public gotoSummary(): void {
        this.cartRoutingService.moveToSummary();
    }

    public onInsuranceChange(insurance: SelectedInsurance): void {
        this.checkout.insurance = insurance;
        this.updateHelper();
    }

    public onEmailFieldClicked(): void {
        this.dialogService.openSnackBarDialog(this.translateService.instant('NOTIFS.MAIL_EDIT_MSG'));
    }

    public onMarkFormAsVisited(markUntil?: string): string {
        let invalidId = '';
        if (this.activeTab === 1) {
            return invalidId;
        }

        const checkFormValidity = (formGroupArr: FormGroup[], formIdPrefix: string) => {
            for (const [idx, formGroup] of formGroupArr.entries()) {
                if (!formGroup) {
                    return false;
                }

                const formId = formIdPrefix + idx;
                if (formGroup.invalid && invalidId === '') {
                    invalidId = formId;
                }

                if (markUntil !== '' && markUntil === formId) {
                    this.tzTravellerService.updateTravellerForm$.next(true);
                    return true;
                }

                Object.keys(formGroup.controls).forEach((key) => {
                    formGroup.controls[key].markAsDirty();
                    formGroup.controls[key].markAsTouched();
                });
            }

            this.tzTravellerService.updateTravellerForm$.next(true);
            return false;
        };

        if (this.travellersForm.invalid) {
            if (checkFormValidity(this.transportFormArray.controls as FormGroup[], 'TRANSPORT_TRAVELER_')) {
                return invalidId;
            }

            if (checkFormValidity(this.hotelFormArray.controls as FormGroup[], 'ACC_TRAVELER_')) {
                return invalidId;
            }

            if (checkFormValidity([this.travellersForm.controls['transfer']] as FormGroup[], 'TRANSFER_TRAVELER_')) {
                return invalidId;
            }

            if (checkFormValidity([this.travellersForm.controls['rental']] as FormGroup[], 'RENTAL_TRAVELER_')) {
                return invalidId;
            }
        }

        if (!this.insuranceSelected) {
            this.insuranceTouched = true;

            if (invalidId === '') {
                invalidId = 'TRAVEL_INSURANCE';
            }
        }

        return invalidId;
    }

    private initializeCheckoutComponent(): void {
        this.cartService.reInit().then((cartData: ICart) => {
            this.loaderService.triggerLoading.emit({ loading: false });
            if (!cartData || !cartData.items || Object.keys(cartData.items).length === 0) {
                this.gotoSummary();
                return;
            }

            this.cartData = cartData;
            this.checkout.transportID = this.getTransportID();
            this.formCheckoutTravellers(cartData);
            this.updateCartInfos();
            this.formCheckoutPayment(this.cartData.helpers.payNowInt > 0);
            this.formDraftValues();
            this.checkSignedIn();

            this.settingsService.selectedLanguageSubject.pipe(skip(1), takeUntil(this.destroy$)).subscribe(() => {
                this.updateCartInfos();
                this.getTravelInsurance();
            });

            this.userService.loggedIn$.pipe(skip(1), takeUntil(this.destroy$)).subscribe((isLogged) => {
                this.formProfileData();
            });
        });
    }

    private updateCartInfos(): void {
        this.cartSummary = this.tzCheckoutService.formCheckoutCartSummary(this.cartData.items);
        this.checkoutLuggages = this.tzCheckoutService.formTransportLuggage(this.cartData);
        this.cartData.helpers = this.tzCheckoutService.getHelpers(
            this.cartData,
            this.checkout.additionalLuggagePrice,
            this.checkout.insurance.selected
        );
    }

    private formCheckoutTravellers(cartData: ICart): void {
        const group: any = {
            transport: this.fb.array([]),
            hotel: this.fb.array([]),
        };
        this.cartInfo = this.tzCheckoutService.formCartTravellersData(cartData);

        if (this.cartInfo.TRANSFER) {
            this.checkout.transferPassenger = { type: TravellerType.ADULT } as CheckoutTraveller;
            group.transfer = this.fb.group(this.tzTravellerService.createTravellerGroup('transfer'));
        }

        if (this.cartInfo.RENTAL) {
            this.checkout.rentalPassenger = {
                type: TravellerType.ADULT,
                selectedAge: this.cartInfo.RENTAL.driversAge,
            } as CheckoutTraveller;

            group.rental = this.fb.group(this.tzTravellerService.createTravellerGroup('rental'));
        }

        this.travellersForm = this.fb.group(group);

        if (this.cartInfo.FLIGHT_TRAIN) {
            const travellers = this.tzTravellerService.newFlightTrainTravellers(this.cartInfo.FLIGHT_TRAIN);
            this.checkout.passengerDetails = travellers;

            for (const traveller of travellers) {
                this.transportFormArray.push(
                    this.fb.group(this.tzTravellerService.createTravellerGroup('transport', traveller.docNotRequired))
                );
            }
        }

        if (this.cartInfo.ACC) {
            const travellers = this.tzTravellerService.newAccTravellers(this.cartInfo.ACC);
            this.checkout.hotelPassengers = travellers;

            for (const traveller of travellers) {
                this.hotelFormArray.push(this.fb.group(this.tzTravellerService.createTravellerGroup('acc')));
            }
        }
    }

    private formCheckoutPayment(formStripe: boolean): void {
        const group: any = {
            profile: this.fb.group(this.tzCheckoutService.getProfileGroup()),
            billing: this.fb.group(this.tzCheckoutService.getBillingGroup()),
        };

        if (formStripe) {
            group.payment = this.fb.group(this.tzCheckoutService.getPaymentGroup());
        }

        this.paymentForm = this.fb.group(group);
        this.tzCheckoutService.setBillingValue(this.paymentForm?.get('billing') as FormGroup);
    }

    private setDraftValues(checkoutDataStr: string): void {
        if (!checkoutDataStr || checkoutDataStr === '') {
            return;
        }

        const checkoutData = JSON.parse(
            this.tzCheckoutService.formCheckoutDraftValues(checkoutDataStr, this.cartInfo, this.checkout, {
                transport: this.transportFormArray,
                hotel: this.hotelFormArray,
            })
        );

        if (!this.paymentFormGroup && checkoutData.paymentFormValue.payment) {
            delete checkoutData.paymentFormValue.payment;
        }

        this.checkout = checkoutData.checkoutData;
        this.getTravelInsurance();
        this.travellersForm.patchValue({ ...this.travellersForm.value, ...checkoutData.travellersFormValue });
        this.paymentForm.patchValue({ ...this.paymentForm.value, ...checkoutData.paymentFormValue });
        // this.switchTab(checkoutData.activeTab);
        this.updateHelper();
        this.draftTravellerInfo();

        this.tzCheckoutService.setBillingValidators(
            this.paymentForm?.get('billing') as FormGroup,
            this.checkout.invoiceRequired
        );

        if (this.cartData.helpers.payNowInt > 0) {
            this.checkout.isStripeValid = false;
        }
    }

    private getTransportID(): string {
        if (this.cartData.items) {
            for (const item of this.cartData.items) {
                if (item.serviceId === ServiceId.FLIGHT) {
                    return item.selectedItem.data.id;
                }
            }
        }

        return '';
    }

    private draftTravellerInfo(): void {
        this.checkout.transportID = this.getTransportID();
        const checkoutDataStr = JSON.stringify({
            travellersFormValue: this.travellersForm.value,
            paymentFormValue: this.paymentForm.value,
            checkoutData: this.checkout,
            activeTab: this.activeTab,
        });

        StorageManagerUtil.set('checkoutFormData', checkoutDataStr);
        this.checkoutDataService
            .setDraftTravellerInfo(this.searchDataService.getCombId(), { data: checkoutDataStr })
            .subscribe(
                () => {},
                (failure: any) => {
                    console.log('Draft update failed: ', failure);
                }
            );
    }

    private formDraftValues(): void {
        const checkoutDataStr = StorageManagerUtil.get('checkoutFormData');
        if (checkoutDataStr !== null) {
            this.setDraftValues(checkoutDataStr);
            return;
        }

        this.checkoutDataService.getDraftTravellerInfo(this.searchDataService.getCombId()).subscribe(
            (response: { data: string }) => {
                this.setDraftValues(response.data);
            },
            (failure: any) => {
                console.log(failure);
                this.getTravelInsurance();
            }
        );
    }

    private getTravelInsurance(gotoPayment = false): void {
        const bookingReq = this.tzCheckoutService.makeBookingReq(this.cartData.items, this.checkout, this.paymentForm);
        this.checkoutDataService.getInsurance(bookingReq).subscribe(
            (response: TzTravelInsurance) => {
                if (!response) {
                    this.onInsuranceChange({
                        selected: {} as Insurance,
                        selectedIdx: null,
                        validatedTC: false,
                    } as SelectedInsurance);
                }

                this.tzTravelInsurances = response;
                const selctedInsurance = this.checkout.insurance;
                const hasSelectedOption =
                    selctedInsurance.selectedIdx !== null &&
                    selctedInsurance.selectedIdx !== -1 &&
                    response.insurances.length > selctedInsurance.selectedIdx;
                if (hasSelectedOption) {
                    const priceChanged =
                        response.insurances[selctedInsurance.selectedIdx].net.listPrice !==
                        selctedInsurance.selected.net.listPrice;
                    if (priceChanged) {
                        this.onInsuranceChange({
                            selected: {} as Insurance,
                            selectedIdx: null,
                            validatedTC: false,
                        } as SelectedInsurance);
                    }

                    if (gotoPayment) {
                        if (priceChanged) {
                            this.insuranceTouched = true;
                            this.scrollService.scrollTo('TRAVEL_INSURANCE', 70, true);
                        } else {
                            this.goto(1);
                        }
                    }
                }
            },
            (failure: any) => {
                console.log(failure);
            }
        );
    }

    private formProfileData(): void {
        const profileValue = this.profileFormGroup.value;
        if (this.userService.IsLogged()) {
            delete profileValue.repeatEmailID;
        } else if (!profileValue.repeatEmailID) {
            profileValue.repeatEmailID = '';
        }

        const profileFormGroup = this.fb.group(this.tzCheckoutService.getProfileGroup());
        if (profileValue.emailID !== '') {
            if (this.userService.IsLogged()) {
                delete profileValue.emailID;
            }
            profileFormGroup.patchValue(profileValue);
        }

        this.paymentForm = this.fb.group({
            billing: this.paymentForm?.get('billing') as FormGroup,
            payment: this.paymentFormGroup,
            profile: profileFormGroup,
        });
    }

    private checkSignedIn(): void {
        const skipSignin = StorageManagerUtil.get('skipSignIn');
        if (skipSignin || this.userService.IsLogged()) {
            return;
        }

        const askToLogin = () => {
            this.userService.openLoginModel(undefined, true).then(
                () => this.formProfileData(),
                (reason) => console.log(reason)
            );
        };

        if (StorageManagerUtil.get('trazler_customer_token', true)) {
            this.userService.getCustomer(false).then(
                () => {},
                () => {
                    askToLogin();
                }
            );
            return;
        }

        askToLogin();
    }

    private goto(tabId: 0 | 1): void {
        this.activeTab = tabId;
        this.scrollService.scrollToTop();
    }
}
