import { Injectable } from '@angular/core';
import { ServiceId } from '@core/constants/service-id.const';
import { CSS_MAPPING } from '@core/constants/css-mapping';
import { CartService } from '@services/modules/cart/services/cart.service';
import { TripPlannerService } from '@services/searchEngine/services/trip-planner.service';
import { SearchDataService } from '@services/searchEngine/services/search-data.service';
import { ListenersService } from '@services/modules/core/listeners-service';
import { ListenerId } from '@core/constants/listener-id.const';
import { ServiceTab } from '@schemas/modules/service-tabs/service-tab';
import { TzCurrencyPipe } from '@shared/pipes/tz-currency.pipe';

@Injectable({
    providedIn: 'root',
})
export class TzServiceTabs {
    private summaryActive = true;
    private active = ServiceId.INVALID;

    private tabInfo: any = {
        enable: false,
        active: false,
        minPrice: '',
        minPriceCur: '',
        resultCount: 0,
        inCart: false,
        selectedItemPrice: '',
        styles: [],
        loader: false,
        preSearch: false,
        loaderIcon: 'loader/tab-spinner',
    };

    private tabs: ServiceTab[] = [
        {
            serviceId: ServiceId.FLIGHT_TRAIN,
            name: 'Flights, Trains',
            label: 'SEARCH.TAB.FLIGHT_TRAINS',
            image: 'flight_service.svg',
            secondImage: 'mob-train-service-tab.svg',
            ordinal: 1,
            // icon: CSS_MAPPING['flight']['icon'],
            secondIconClass: CSS_MAPPING.train.iconClass,
            iconClass: CSS_MAPPING.flight.iconClass,
            //colorCode: CSS_MAPPING.flight.color,
            tabletLabel: 'SEARCH.TAB.FLIGHT_TRAINS_TABLET',
            mobLabel: 'SEARCH.TAB.FLIGHT_TRAINS_MOB',
            mobIcon: 'mob-flight-service-tab',
            secondMobIcon: 'mob-train-service-tab',
            ...this.tabInfo,
        },
        {
            serviceId: ServiceId.ACC,
            name: 'Hotels, Residence',
            label: 'SEARCH.TAB.HOTELS_RESIDENCES',
            image: 'hotel_service.svg',
            ordinal: 2,
            icon: CSS_MAPPING.hotel.icon,
            iconClass: CSS_MAPPING.hotel.iconClass,
            //colorCode: CSS_MAPPING.hotel.color,
            tabletLabel: 'SEARCH.TAB.HOTELS_RESIDENCES_TABLET',
            mobLabel: 'SEARCH.TAB.HOTELS_RESIDENCES_MOB',
            mobIcon: 'mob-hotels-service-tab',
            ...this.tabInfo,
        },
        {
            serviceId: ServiceId.TRANSFER,
            name: 'Transfers',
            label: 'SEARCH.TAB.TRANSFERS',
            image: 'Transfers.svg',
            ordinal: 3,
            icon: CSS_MAPPING.transfer.icon,
            iconClass: CSS_MAPPING.transfer.iconClass,
            //colorCode: CSS_MAPPING.transfer.color,
            tabletLabel: 'SEARCH.TAB.TRANSFERS_TABLET',
            mobLabel: 'SEARCH.TAB.TRANSFERS_MOB',
            mobIcon: 'mob-transfer-service-tab',
            ...this.tabInfo,
        },
        {
            serviceId: ServiceId.CAR_RENTAL,
            name: 'Car Rentals',
            label: 'SEARCH.TAB.CAR_RENTALS',
            image: 'car.svg',
            ordinal: 4,
            icon: CSS_MAPPING.rentcar.icon,
            iconClass: CSS_MAPPING.rentcar.iconClass,
            //colorCode: CSS_MAPPING.rentcar.color,
            tabletLabel: 'SEARCH.TAB.CAR_RENTALS_TABLET',
            mobLabel: 'SEARCH.TAB.CAR_RENTALS_MOB',
            mobIcon: 'mob-car-service-tab',
            ...this.tabInfo,
        },
    ];

    private summaryTab: ServiceTab = {
        serviceId: ServiceId.SUMMARY,
        name: 'Basket',
        label: 'GENERAL.SUMMARY',
        image: 'total.svg',
        ordinal: 5,
        color: 'purple-color',
        icon: 'icon-total',
        ...this.tabInfo,
        mobLabel: 'GENERAL.SUMMARY',
        tabletLabel: 'GENERAL.SUMMARY',
        loaderIcon: 'loader/tab-spinner-summary',
        styles: ['summary'],
    };

    constructor(
        private cartService: CartService,
        private tripPlannerService: TripPlannerService,
        private searchDataService: SearchDataService,
        private listenersService: ListenersService,
        public currencyPipe: TzCurrencyPipe
    ) {}

    private static getSummaryI18nKey(activeServiceId: ServiceId, allBooked?: boolean): string {
        if (activeServiceId === ServiceId.SUMMARY || allBooked) {
            return 'GENERAL.BASKET';
        } else {
            return 'GENERAL.SUMMARY';
        }
    }

    public reformTabFields(tab: ServiceTab, activeServiceId: ServiceId, allBooked?: boolean) {
        let cartInfo = this.cartService.getCartInfo(tab.serviceId);
        let serviceId = tab.serviceId;

        tab.disable = this.isTabDisabled(tab, serviceId, activeServiceId, allBooked);
        if (tab.serviceId === ServiceId.SUMMARY) {
            tab.label = TzServiceTabs.getSummaryI18nKey(activeServiceId, allBooked);
            tab.tabletLabel = tab.label;
            tab.mobLabel = tab.label;
        }

        if (tab.serviceId === ServiceId.FLIGHT_TRAIN) {
            const info = this.cartService.getCartInfo(ServiceId.TRAIN);
            if (info.inCart) {
                serviceId = ServiceId.TRAIN;
                tab.image = 'train-blue.svg';
                tab.icon = 'service-icon-train';
                tab.iconClass = CSS_MAPPING.train.iconClass;
                cartInfo = info;
            } else {
                serviceId = ServiceId.FLIGHT;
                tab.icon = 'service-icon-flight';
                tab.image = 'flight_service.svg';
                tab.iconClass = CSS_MAPPING.flight.iconClass;
            }
            if (
                activeServiceId === ServiceId.TRAIN ||
                activeServiceId === ServiceId.FLIGHT ||
                activeServiceId === ServiceId.FLIGHT_TRAIN
            ) {
                serviceId = activeServiceId;
            }
        }

        const resultInfo = this.searchDataService.getSearchResult(serviceId);
        if (resultInfo) {
            tab.resultCount = resultInfo.resultLength;
        }
        tab.styles.push(tab.color);
        tab.enable = tab.inCart = cartInfo.inCart;
        tab.selectedItemPrice = cartInfo.price;
        tab.active = serviceId === activeServiceId;
        tab.isVirtual = cartInfo.isVirtual;
        tab.selectedItemPriceCur = cartInfo.currency;
        if (tab.enable) {
            if (cartInfo.price === 0 && cartInfo.inError) {
                tab.isInError = true;
                tab.minPrice = cartInfo.inError.cheapestListPrice;
                tab.minPriceCur = cartInfo.inError.cheapestListPriceCur;
            } else {
                tab.isInError = false;
                tab.minPrice = cartInfo.price;
                tab.minPriceCur = cartInfo.currency;
            }
            if (cartInfo.isVirtual) {
                tab.preSearch = true;
            }
        }
        if (!tab.enable || cartInfo.isVirtual) {
            const preSearchInfos = this.tripPlannerService.get(tab.serviceId);
            if (preSearchInfos.enable || preSearchInfos.complete) {
                tab.preSearch =
                    ((preSearchInfos.enable || preSearchInfos.complete) && !preSearchInfos.loader) ||
                    cartInfo.isVirtual;
                tab.loader = preSearchInfos.loader;
                if (cartInfo.isVirtual && cartInfo.price) {
                    tab.minPrice = cartInfo.price;
                    tab.minPriceCur = cartInfo.currency;
                } else {
                    tab.minPrice = 0;
                }
            } else {
                tab.minPrice = cartInfo.price;
                tab.minPriceCur = cartInfo.currency;
            }
        }

        if (tab.active) {
            if (tab.serviceId === ServiceId.FLIGHT_TRAIN) {
                tab.loader =
                    this.tripPlannerService.get(ServiceId.FLIGHT).loader ||
                    this.tripPlannerService.get(ServiceId.TRAIN).loader;
            } else {
                const preSearchInfos = this.tripPlannerService.get(tab.serviceId);
                tab.loader = preSearchInfos.loader;
            }
        }

        tab.loader = tab.forceLoader ? true : tab.loader;

        return {
            active: tab.active,
            tab,
        };
    }

    public setActive(newActive: any) {
        this.active = newActive;
        this.listenersService.NotifyListeners(ListenerId.LISTEN_TAB_CHANGED, newActive);
    }

    public getActive() {
        return this.active;
    }

    public isSummaryActive() {
        return this.summaryActive;
    }

    public setSummaryActive(state: boolean) {
        this.summaryActive = state;
    }

    public toggleForceLoader(serviceId: ServiceId, force: boolean): void {
        for (const tab of this.tabs) {
            if (tab.serviceId === serviceId) {
                tab.forceLoader = force;
                this.listenersService.NotifyListeners(ListenerId.LISTEN_TAB_CHANGED);
                break;
            }
        }
    }

    getTabs() {
        return this.tabs;
    }

    public getSummaryTab() {
        return { ...this.summaryTab, styles: ['summary'] };
    }

    public updatePriceChanging(oldPrice: number, currentPrice: number): string {
        const priceDiff = currentPrice - oldPrice;
        const priceDiffStrWithCur = this.currencyPipe.transform(Math.abs(priceDiff));
        return priceDiff > 0 ? `+ ${priceDiffStrWithCur}` : `- ${priceDiffStrWithCur}`;
    }

    private isTabDisabled(tab: any, serviceId: ServiceId, activeServiceId: ServiceId, allBooked?: boolean): boolean {
        const summaryTabAndNotAllServicesAreBooked = serviceId === ServiceId.SUMMARY && !allBooked;
        const notSummaryTabAndServiceIsBookedAndActiveServiceIdIsNotEqualToSummary =
            serviceId !== ServiceId.SUMMARY &&
            this.cartService.isAllServiceOptionBooked(tab.serviceId) &&
            (activeServiceId !== ServiceId.SUMMARY || tab.selectedService);
        return (
            summaryTabAndNotAllServicesAreBooked || notSummaryTabAndServiceIsBookedAndActiveServiceIdIsNotEqualToSummary
        );
    }
}
